<template>
    <nav class="navbar is-transparent">
        <div class="navbar-brand">
            <a class="navbar-item" href="#">
                <img
                    src="@/assets/frutek-logo.png"
                    alt="Bulma: a modern CSS framework based on Flexbox"
                    width="200"
                    height="100"
                />
            </a>
            <div class="navbar-burger burger" data-target="navMenubd-example">
                <span></span>
                <span></span>
                <span></span>
            </div>
        </div>

        <div id="navMenubd-example" class="navbar-menu">
            <div class="navbar-end">
                <a class="navbar-item" v-scroll-to="'#aboutUs'">Quiénes Somos</a>
                <a class="navbar-item" v-scroll-to="'#products'">Productos</a>
                <a class="navbar-item" v-scroll-to="'#services'">Servicios</a>
                <!-- <a class="navbar-item" v-scroll-to="'#information'">Información Técnica</a> -->
                <a class="navbar-item" v-scroll-to="'#contact'">Contacto</a>
            </div>
        </div>
    </nav>
</template>

<script>
export default {
    data() {
        return {}
    },
}

document.addEventListener('DOMContentLoaded', function() {
    // Get all "navbar-burger" elements
    var $navbarBurgers = Array.prototype.slice.call(
        document.querySelectorAll('.navbar-burger'),
        0
    )

    // Check if there are any nav burgers
    if ($navbarBurgers.length > 0) {
        // Add a click event on each of them
        $navbarBurgers.forEach(function($el) {
            $el.addEventListener('click', function() {
                // Get the target from the "data-target" attribute
                var target = $el.dataset.target
                var $target = document.getElementById(target)

                // Toggle the class on both the "navbar-burger" and the "navbar-menu"
                $el.classList.toggle('is-active')
                $target.classList.toggle('is-active')
            })
        })
    }
})
</script>

